
/************************/
/*     12. Projects     */
/************************/
.filter {
	padding-top: 6.5rem;
	padding-bottom: 7rem;


    .section-title {
        margin-bottom: 0.5rem;
    }
    
    h2,
    .section-title {
        text-align: center;
    }
    
    h2 {
        margin-bottom: 3.25rem;
    }
    
    .button-group {
        text-align: center;
        a {
            display: inline-block;
            margin-right: 0.1875rem;
            margin-bottom: 0.5rem;
            margin-left: 0.1875rem;
            padding: 0.3125rem 1.375rem 0.25rem 1.375rem;
            border-radius: 0.25rem;
            background-color: #f1f4f7;
            color: #7b7e85;
            font: 700 0.75rem/1.25rem "Montserrat", sans-serif;
            cursor: pointer;
            transition: all 0.2s ease;
        }
        
        a:hover {
            background-color: #F6A64F;
            color: #fff;
        }
        
        a.button.is-checked {
            background-color: #F6A64F;
            color: #fff;
        }
    }
    
    
    .filter-container {
        margin-top: 1.25rem;
        border-radius: 0.375rem;
        position: unset;
    }
    
    /* Hover Animation */
    .element-item {
        position: relative;
        float: left;
        overflow: hidden;
        width: 50%;
        background-color: #000; /* to hide 1px white margin in IE */
    }
    
    .element-item img {
        max-width: 100%;
        margin: 0;
        transition: all 0.3s;
    }
    
    .element-item:hover img {
        -moz-transform: scale(1.15);
        -webkit-transform: scale(1.15);
        transform: scale(1.15);
    }
    /* end of hover animation */
    
    .element-item .element-item-overlay {
        position: absolute;
        z-index: 2;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.4);
        opacity: 1;
        text-align: center;
        transition: all 0.3s;
    }
    
    .element-item .element-item-overlay span {
        position: absolute;
        z-index: 3;
        top: 42%;
        right: 0;
        left: 0;
        color: #fff;
        font: 700 1.125rem/1.625rem "Montserrat", sans-serif;
    }
}


